<template>
  <div class="firenetworkingBox">
    <!--  -->
    <div class="top_tips">
      <div class="tooltip">
        <el-button  type="primary" @click="screen('')" plain round>
          
          <i v-if="screensign == ''" class="el-icon-check  el-icon--right"></i>
        </el-button>
        <el-button @click="screen('')" type="success" plain round>
          {{firenetworkState.online}}
          <i v-if="screensign == ''" class="el-icon-check  el-icon--right"></i>
        </el-button>
        <el-button @click="screen('')" type="info" plain round>
          {{firenetworkState.offline}}
          <i v-if="screensign == ''" class="el-icon-check  el-icon--right"></i>
        </el-button>
      </div>
      
      <el-button @click="addNew"></el-button>
    </div>
    
    <!--  -->
    <baseTable 
      v-loading="loading"
      element-loading-text=""
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :tableThead="tableThead" 
      :tableData="tableData"
      :ishownetworkBtn="true"
      :pageSize="10"
      :totalCount="totalCount"
      @pageChange="AlarmDeviceList_prev_next"
      @getnetwork="getnetwork"
      @getdialogContent="getdialogContent"
      @showdetails="showdetails"
      @deleteInfo="deleteTotal"
    >
    </baseTable>

    <!--  -->
    <baseDialog @closeDialog="closeDialog" ref="baseDialog" title="">
      <el-form v-loading="loading" class="formBox" :rules="rules" ref="form" :model="equmentform" label-width="80px">
        <!--  -->
        <el-row class="baseBox" :gutter="20">
          <el-col class="left_box" :span="4">
            <div class="grid-content basetitle"></div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content">
              <el-row class="pd20" :gutter="20">
                <el-col :span="12">
                  <el-form-item  label="" prop="deviceModel">
                    <el-select  v-model="equmentform.deviceModel" placeholder="">
                      <el-option 
                      v-for='item in DeviceTypes'
                      :key="item"
                      :label="item" 
                      :value="item"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                    <p class="marginleft10"></p>
                </el-col>
              </el-row>
              <el-row class="pd20" :gutter="20">
                <el-col :span="12">
                  <el-form-item  label="" prop="deviceSn">
                    <el-input  v-model="equmentform.deviceSn" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
                <el-col v-if="equmentform.deviceId" :span="12">
                    <p class="marginleft10" v-if="equmentform.state == ''"><span class="green">{{equmentform.state}}</span></p>
                    <p class="marginleft10" v-if="equmentform.state == ''"><span class="red">{{equmentform.state}}</span></p>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row class="baseBox" :gutter="20">
          <el-col class="left_box" :span="4">
            <div class="grid-content basetitle"></div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content">
              <el-row class="pd20" :gutter="20">
                <el-col :span="12">
                    <el-form-item  label="" prop="fireUnitArchitectureId">
                      <el-select :disabled="equmentform.deviceId ? true :false"  v-model="equmentform.fireUnitArchitectureId" placeholder="">
                        <el-option 
                         v-for="item in buildList"
                        :key="item.id"
                        :label="item.name" 
                        :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                </el-col>
                <el-col class="networkNum" :span="12">
                  <el-form-item  label="">
                    <el-input  v-model="equmentform.brand" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row class="pd20" :gutter="20">
                <el-col :span="12">
                  <el-form-item  label="" prop="protocol">
                    <el-select  v-model="equmentform.protocol" placeholder="">
                      <el-option 
                        v-for="item in DeviceProtocols"
                        :key="item"
                        :label="item" 
                        :value="item"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col class="networkNum" :span="12">
                  <el-form-item  label="" prop="netDetectorNum">
                    <el-input  v-model.number="equmentform.netDetectorNum" placeholder=""></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row class="baseBox" :gutter="20">
          <el-col class="left_box" :span="4">
            <div class="grid-content basetitle"></div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content">
              <!--  -->
              <el-row class="pd20">
                <el-col :span="24">
                  <el-form-item  label="" prop="Model">
                    <el-checkbox-group v-model="equmentform.enableFault">
                      <el-checkbox label=""></el-checkbox>
                      <el-checkbox label=""></el-checkbox>
                      <el-checkbox label=""></el-checkbox>
                    </el-checkbox-group>
                  </el-form-item>
                </el-col>
              </el-row>

              <el-row class="pd20">
                <el-col :span="24">
                  <el-form-item  label="" prop="Model">
                    <el-checkbox-group v-model="equmentform.enableAlarm">
                      <el-checkbox label=""></el-checkbox>
                      <el-checkbox label=""></el-checkbox>
                      <el-checkbox label=""></el-checkbox>
                    </el-checkbox-group>
                  </el-form-item>
                    <el-col  v-if="equmentform.enableAlarm.indexOf('') !=-1 ||equmentform.enableFault.indexOf('') !=-1" class="smsPhones" :span="16">
                      <el-form-item  label="">
                        <el-input  v-model="equmentform.smsPhones" placeholder="()"></el-input>
                      </el-form-item>
                    </el-col>
                </el-col>
              </el-row>
              <div>
                <!--  -->
                <el-row  class="pd20" :gutter="20">
                    <el-col :span="12">
                        <el-form-item  label="" prop="comType">
                          <el-select  v-model="equmentform.comType" placeholder="">
                            <el-option 
                              v-for="item in types"
                              :key="item"
                              :label="item" 
                              :value="item"></el-option>
                          </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item  label="" prop="baudRate">
                          <el-select  v-model="equmentform.baudRate" placeholder="">
                            <el-option 
                              v-for="item in Baudrates"
                              :key="item"
                              :label="item" 
                              :value="item"></el-option>
                          </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <!--  -->
                <el-row class="pd20" :gutter="20">
                  <el-col :span="12">
                    <el-form-item  label="" prop="netComm">
                      <el-select  v-model="equmentform.netComm" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="WIFI" value="WIFI"></el-option>
                        <el-option label="NB-IOT" value="NB-IOT"></el-option>
                        <el-option label="4G" value="4G"></el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                </el-row>
              </div>
              
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row  class="btns" :gutter="20">
          <el-button @click="add_update_equment"></el-button>
          <el-button @click="cancel(equmentform.deviceId)"></el-button>
        </el-row>
      </el-form>
          
    </baseDialog>

    <!--  -->
    <baseDialog class="networkDialog" ref="networkDialog" title="">
      <div class="topBox">
        <el-button @click="Import"></el-button>
        <el-button @click="addNetworkcomponents" class="add"></el-button>
      </div>
      <baseTable 
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)" 
        :totalCount="networktotalCount"
        :pageSize="5"
        :tableThead="networkThead"
        :tableData="networkData"
        :currentpage="networkPage.current"
        @pageChange="network_prev_next"
        @showdetails="showdetails_network"
        @deleteInfo="DeleteDetector"
      ></baseTable>
    </baseDialog>
    <!--  -->
    <baseDialog  @closeDialog="closeDialog" ref="network_detailDialog" title="">
      <el-form  ref="network_detail"  :rules="networkFormrules"  class="network_detail-form" label-width="120px" :model="networkForm">
            <el-form-item label="" prop="identify">
                <el-input v-model="networkForm.identify" placeholder=""></el-input>
            </el-form-item>
            <el-form-item  label="" prop="detectorTypeName">
                <el-select  v-model="networkForm.detectorTypeName" placeholder="">
                    <el-option 
                    v-for="item in FireAlarmDetectorTypes"
                    :key="item"
                    :label="item" 
                    :value="item"></el-option>
                </el-select>
            </el-form-item>
            <p class="build">
              
              <span>{{networkForm.fireUnitArchitectureName}}</span>
            </p>
            <el-form-item  label="" prop="fireUnitArchitectureFloorId">
              <el-select  v-model="networkForm.fireUnitArchitectureFloorId" placeholder="">
                  <el-option 
                  v-for="item  in fireUnitArchitecturefloors" 
                  :key="item.id"
                  :label="item.name" 
                  :value="item.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="location">
                <el-input v-model="networkForm.location" placeholder=""></el-input>
            </el-form-item>
            <el-form-item >
              <el-button @click="add_updatenetwork"></el-button>
              <el-button @click="network_cancel" class="cancel"></el-button>
            </el-form-item>


      </el-form>  
    </baseDialog>
    <!--  -->
    <baseDialog class="network_importDialog" ref="network_importDialog" title="">
      <div class="dialog_content">
        <p class="tips"></p>
        <div class="build">
          <p>
            <span></span>
            <span>{{this.networkForm.fireUnitArchitectureName}}</span>
          </p>
          <!-- <div>
            <span></span>
            <input type="file">
          </div> -->
        </div>
        <el-upload
            class="upload-demo"
            ref="upload"
            action=""
            name="excelFile"
            :multiple="false"
            :limit="1"
            :on-exceed="handleExceed"
            :http-request="uploadFile"
            :before-upload="beforeAvatarUpload"
            :file-list="fileList"
            :auto-upload="false"
        >
            <el-button slot="trigger" size="small" type="primary"></el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
            <div slot="tip" class="el-upload__tip">
              <p>excel</p> 
            </div>
        </el-upload>

        <div class="bottom">
          <span @click="download"></span>
        </div>
      </div>
    </baseDialog>
    <!--  -->
    <baseDialog class="threedialog" ref="faultListDialog" title="">
      <baseTable
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)" 
        :tableThead="faultThead"
        :tableData="faultData"
        :pageSize="5"
        :totalCount="faultTotalCount"
        @pageChange="fault_next_prevPage"
      >
      </baseTable>
    </baseDialog>
    <!-- 30 -->
    <baseDialog class="threedialog" ref="fireRecord_highFrequencyDialog" :title="alarm30_high_title">
      <div class="top_tips">{{Alarm30Day_toptips_deviceSn}} <span>{{Alarm30Day_toptips_fireUnitArchitectureName}}</span></div>
      <baseTable 
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :tableThead="Alarm30Day_highThead"
        :tableData="Alarm30Day_highData"
        :pageSize="5"
        :currentpage="Alarm30Day_highPage.current"
        :totalCount="Alarm30Day_highTotal"
        @pageChange="next_prevPage"
      >
      </baseTable>
    </baseDialog>
    <!--  -->
    <!-- <baseDialog ref="highFrequencyDialog" title="30">
    </baseDialog> -->

  </div>  
 

</template>

<script>
import baseTable from '../../../components/baseTable/index'
import baseDialog from '../../../components/baseDialog'
import { error } from 'util';
export default {
  components:{
    baseTable,
    baseDialog
  },
  data(){
    //
      var checknetDetectorNum = (rule, value, callback) => {
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error(''));
          }else{
             callback();
          }
        }, 1000);
      };
    return{
      firenetworkState:{
        online:0,
        offline:0
      },
      screensign:'',
      page:{
        FireUnitId:localStorage.getItem('fireUnitID'),
        SkipCount:0,
        MaxResultCount:10,
      },
      totalCount:0,//
      buildList:[],//
      DeviceTypes:[],//
      DeviceProtocols:[],//
      Baudrates:['1200','2400','4800','9600','14400','19200','28800','38400','57600','115200'],
      types:['232','485'],
      tableThead:[
        {
          name:'',
          prop:'deviceSn'
        },
        {
          name:'',
          prop:'fireUnitArchitectureName'
        },
        {
          name:'',
          prop:'state'
        },
        {
          name:'',
          prop:'netDetectorNum'
        },
        {
          name:'',
          prop:'faultDetectorNum'
        },
        {
          name:'',
          prop:'detectorFaultRate'
        },
        {
          name:'30',
          prop:'alarmNum30Day'
        },
        {
          name:'',
          prop:'highAlarmDetectorNum'
        },
        {
          name:'',
          width:'200px'
        },
      ],
      tableData:[],
      equmentform:{
        enableAlarm:[],
        enableFault:[],
      },
      equmentform1:{
        deviceModel:'TSJ-CS10',
        enableAlarm:[''],
        enableFault:[''],
        baudRate:'9600',
        comType:'232'
      },//
      rules:{
        deviceModel: [
            { required: true, message: "", trigger: "change" }
          ],
        deviceSn: [
            { required: true, message: "", trigger: "blur" }
        ],
        netDetectorNum:[
          { validator: checknetDetectorNum, trigger: 'blur' }
        ]
      },
      PortParam:{},//
      //-------------------------------------------------------
      networkFormrules:{
        identify: [
          { required: true, message: "", trigger: "blur" }
        ],
      },
      networkThead:[
        {
          name:'',
          prop:'identify'
        },
        {
          name:'',
          prop:'detectorTypeName'
        },
        {
          name:'',
          prop:'location'
        },
        {
          name:'',
          prop:'state'
        },
        {
          name:''
        }
      ],
      networkData:[],
      networkForm:{},//form
      FireAlarmDetectorTypes:[],//
      fireUnitArchitecturefloors:[],//
      networktotalCount:0,//table
      networkPage:{
        SkipCount:0,
        MaxResultCount:5,
        current:1
      },
      fileList:[],
      //----------------------------------------
      FaultPage:{
        SkipCount:0,
        MaxResultCount:5,
        current:1
      },//table
      faultTotalCount:0,//table
      //table
      faultThead:[
        {
          name:"",
          prop:'identify'
        },
        {
          name:"",
          prop:'detectorTypeName'
        },
        {
          name:"",
          prop:'fireUnitArchitectureFloorName'
        },
        {
          name:"",
          prop:'location'
        },
        {
          name:"",
          prop:'faultContent'
        },
        {
          name:"",
          prop:'faultTime'
        },
      ],
      faultData:[],
      //---------------
      alarm30_high_title:'30',
      Alarm30Day_highPage:{
        SkipCount:0,
        MaxResultCount:5,
        current:1
      },
      Alarm30Day_highTotal:0,
      Alarm30Day_highThead:[],
      Alarm30Day_highData:[],
      Alarm30Day_toptips_fireUnitArchitectureName:'',
      Alarm30Day_toptips_deviceSn:'',
      highThead:[
        {
          name:"",
          prop:'identify'
        },
        {
          name:"",
          prop:'detectorTypeName'
        },
        {
          name:"",
          prop:'fireUnitArchitectureFloorName'
        },
        {
          name:"",
          prop:'location'
        },
        {
          // name:"30",
          name:"",                                                                                                      
          prop:'alarmNum'
        },
      ],
      Alarm30DayThead:[
        {
          name:"",
          prop:'identify'
        },
        {
          name:"",
          prop:'detectorTypeName'
        },
        {
          name:"",
          prop:'fireUnitArchitectureFloorName'
        },
        {
          name:"",
          prop:'location'
        },
        {
          name:"",
          prop:'alarmTime'
        },
      ],

      loading:false

    }
  },
  created(){
      this.GetFireAlarmDeviceStateNum();
      this.GetListByFireUnitId();//
      this.GetFireAlarmDeviceList();
      this.GetFireAlarmDeviceModels();
  },
  methods:{
    GetFireAlarmDeviceStateNum(){
      this.$axios.get(this.$api.GetFireAlarmDeviceStateNum,{params:this.page}).then(res=>{
        console.log("res",res);
        this.firenetworkState.online = res.data.result[0].value
        this.firenetworkState.offline = res.data.result[1].value
      })
    },
    //
    screen(text){
      this.screensign = text
      this.page.DeviceState = text;
      this.GetFireAlarmDeviceList();
    },
    //table
    GetFireAlarmDeviceList(){
      this.loading = true
      this.$axios.get(this.$api.GetFireAlarmDeviceList,{params:this.page}).then(res=>{
        console.log("GetFireAlarmDeviceList",res)
         this.loading = false;
        
        ({items:this.tableData ,totalCount:this.totalCount} = res.data.result);
        if(res.data.result.items.length == 0 && res.data.result.totalCount >0){
            this.page.SkipCount = 0
            this.GetFireAlarmDeviceList()
        }
        
      }).catch(err=>{
        console.log('err',err)
      })
    },
    //
    AlarmDeviceList_prev_next(page){
      this.page.SkipCount = (page-1)*this.page.MaxResultCount
      this.GetFireAlarmDeviceList();
    },
    //
    GetListByFireUnitId(){
      let fireunitId = localStorage.getItem('fireUnitID')
      this.$axios.get(this.$api.GetListByFireUnitId,{params:{fireunitId}}).then(res=>{
        console.log("",res)
        if(res.data.result.length>0){
          let arrlist =[];
          for(let arr of res.data.result){
           arrlist.push({name:arr.name,id:arr.id})
          }
          console.log("arrlist",arrlist)
          this.buildList = arrlist
        }
      }).catch(error=>{
        console.log("error",error)
      })
    },
    ///
    GetFireAlarmDeviceModels(){
      this.$axios.all([
        this.$axios.get(this.$api.GetFireAlarmDeviceModels,{params:{}}),
        this.$axios.get(this.$api.GetFireAlarmDeviceProtocols,{params:{}})
      ]).then(this.$axios.spread((data1,data2)=>{
        console.log("data1",data1)
        console.log("data2",data2)
        if(data1.data.result.length>0){
          this.DeviceTypes = data1.data.result
        }
        if(data2.data.result.length>0){
          this.DeviceProtocols = data2.data.result
          //  this.DeviceProtocols =['test1']
        }

      })).catch(this.$axios.spread((data1,data2)=>{
        console.log("data1",data1)
        console.log("data2",data2)
      }))
    },
    //
    add_update_equment(){
      this.$refs.form.validate(valid => {
        if(valid){
            this.equmentform.fireUnitId = localStorage.getItem('fireUnitID')
            console.log("this.equmentform",this.equmentform)
            let as = this.equmentform.deviceId ?
            this.$axios.put(this.$api.UpdateFireAlarmDevice,this.equmentform):
            this.$axios.post(this.$api.AddFireAlarmDevice,this.equmentform);
            as.then(res=>{
              console.log("",res)
              if(res.data.success){
                this.$message.success(
                    `${this.equmentform.deviceId ? "" : ""}`
                );
                 this.$refs.baseDialog.dialogVisible  = false;
                 this.GetFireAlarmDeviceList();
              }
            }).catch(err=>{
              console.log("",err)
            })
        }
      })
     
    },
    //quxiao
    cancel(deviceId){
      if(deviceId){
        this.$refs.baseDialog.dialogVisible = false
      }else{
        let stringform = JSON.stringify(this.equmentform1)
        this.equmentform = JSON.parse(stringform)
        
      }
    },
     //
    addNew(){
      this.$refs.baseDialog.dialogVisible = true
      let stringform = JSON.stringify(this.equmentform1) 
      this.equmentform = JSON.parse(stringform)
      let contractPhone = localStorage.getItem('contractPhone')
      this.$set(this.equmentform,'smsPhones',contractPhone)

    },
    //
    closeDialog(name){
      if(name == ''){
        this.$refs.network_detail.resetFields();
      }else if(name == ''){
        this.$refs.form.resetFields();
      }
    },
    //
    deleteTotal(item){
      console.log(item)
      let DeviceId = item.deviceId
      this.$axios.delete(this.$api.DeleteFireAlarmDevice,{params:{DeviceId}}).then(res=>{
          console.log("",res)
        if(res.data.success){
          this.$message({
            message: '',
            type: 'success'
          });
          this.GetFireAlarmDeviceList()
        }
      
      }).catch(err=>{
        console.log("",err)
      })
    },
    // table
    showdetails(item){
      this.$refs.baseDialog.dialogVisible = true
      let DeviceId = item.deviceId
      this.$axios.all([
        this.$axios.get(this.$api.GetSerialPortParam,{params:{DeviceId}}),
        this.$axios.get(this.$api.GetFireAlarmDevice,{params:{DeviceId}})
      ]).then(this.$axios.spread((data1,data2)=>{
        console.log("",data1)
        if(data1.data.success){
          this.PortParam = data1.data.result;
        }
        console.log("",data2)
        if(data2.data.success){
          let string = JSON.stringify(data2.data.result)
          this.equmentform = JSON.parse(string)
        }
      }))
    },
    //
    refreshPortParam(){
      this.loading = true
      let DeviceId = this.equmentform.deviceId
      this.$axios.get(this.$api.GetSerialPortParam,{params:{DeviceId}}).then(res=>{
        console.log("",res)
        if(res.data.success){
          this.loading =false;
          this.PortParam = res.data.result;
          this.$message({
            message: '',
            type: 'success'
          });
        }
      }).catch(err=>{
        console.log("",err)
      })
    },
    


    //--------------30____-------------------------------
    getdialogContent(item,name){
      console.log("name",name,item)
      if(name == ''){
        this.$refs.faultListDialog.dialogVisible = true
        this.FaultPage.fireAlarmDeviceId = item.deviceId
        this.GetFireAlarmFaultDetectorList()
      }else if(name == '30'){
        this.alarm30_high_title = '30'
        this.$refs.fireRecord_highFrequencyDialog.dialogVisible = true
        this.Alarm30Day_highPage.fireAlarmDeviceId = item.deviceId
        this.Alarm30Day_toptips_deviceSn = item.deviceSn //
        this.Alarm30Day_toptips_fireUnitArchitectureName = item.fireUnitArchitectureName //
        this.Alarm30Day_highThead = this.Alarm30DayThead
        this.Alarm30Day_highPage.SkipCount = 0
        this.Alarm30Day_highPage.current = 1
        this.GetFireAlarm30DayList(this.Alarm30Day_highPage)
      }else if(name ==''){
        this.alarm30_high_title = ''
        this.Alarm30Day_highThead = this.highThead
        this.Alarm30Day_highPage.fireAlarmDeviceId = item.deviceId
        this.Alarm30Day_toptips_deviceSn = item.deviceSn //
        this.Alarm30Day_toptips_fireUnitArchitectureName = item.fireUnitArchitectureName //
        this.Alarm30Day_highPage.SkipCount = 0
        this.Alarm30Day_highPage.current = 1
        console.log('Alarm30Day_highPage',this.Alarm30Day_highPage)
        this.$refs.fireRecord_highFrequencyDialog.dialogVisible = true
        this.GetFireAlarmHighList(this.Alarm30Day_highPage)
      }
    },
    //list
    GetFireAlarmFaultDetectorList(){
      this.loading = true
      this.$axios.get(this.$api.GetFireAlarmFaultDetectorList,{params:this.FaultPage}).then(res=>{
        this.loading = false
        console.log("",res);
        ({items:this.faultData,totalCount:this.faultTotalCount} = res.data.result);
      }).catch(err=>{
        console.log("",err)
      })
    },
    //---------
    fault_next_prevPage(page){
      this.FaultPage.SkipCount = (page-1)*this.FaultPage.MaxResultCount
      this.FaultPage.current = page
      this.GetFireAlarmFaultDetectorList(this.FaultPage)
      
    },
    //30
    GetFireAlarm30DayList(Alarm30Day_highPage){
      this.loading = true;//table
      this.$axios.get(this.$api.GetFireAlarm30DayList,{params:Alarm30Day_highPage}).then(res=>{
        console.log("30",res);
        this.loading = false;//
        ({items:this.Alarm30Day_highData,totalCount:this.Alarm30Day_highTotal} = res.data.result);
        for(let arr of this.Alarm30Day_highData){
         
          arr.alarmTime = this.deal(arr.alarmTime)
        }
      }).catch(err=>{
        console.log("30",err)
      })
    },
    deal(data){
      var str = data.split('T');
      var str2 = str[1].split(".")
      var timestr = str[0] +" "+ str2[0]
      return timestr;
    },
    //
    GetFireAlarmHighList(Alarm30Day_highPage){
      this.loading = true;//table
       this.$axios.get(this.$api.GetFireAlarmHighList,{params:Alarm30Day_highPage}).then(res=>{
        console.log("",res);
         this.loading = false;//
        ({items:this.Alarm30Day_highData,totalCount:this.Alarm30Day_highTotal} = res.data.result)
      }).catch(err=>{
        console.log("",err)
      })
    },
    //
    next_prevPage(page){
      console.log("page",page)
      this.Alarm30Day_highPage.current = page
      this.Alarm30Day_highPage.SkipCount = (page-1)*this.Alarm30Day_highPage.MaxResultCount;
      console.log("this.Alarm30Day_highPage",this.Alarm30Day_highPage)
      if(this.alarm30_high_title == '30'){
        this.GetFireAlarm30DayList(this.Alarm30Day_highPage)
      }else if(this.alarm30_high_title == ''){
        this.GetFireAlarmHighList(this.Alarm30Day_highPage)
      }
     
    },
    //----------------------------------------------------------------
    //table
    getnetwork(item){
      console.log("getnetworkTable",item)
     /*  this.networkForm.fireUnitArchitectureName = item.fireUnitArchitectureName
      this.networkForm.fireUnitArchitectureId = item.fireUnitArchitectureId
      this.networkForm.deviceSn = item.deviceSn */
     
      this.networkForm.fireUnitArchitectureName = item.fireUnitArchitectureName
      let networkForm={}
      networkForm.fireUnitArchitectureName = item.fireUnitArchitectureName
      networkForm.fireUnitArchitectureId = item.fireUnitArchitectureId
      networkForm.deviceSn = item.deviceSn
      networkForm.fireAlarmDeviceId = item.fireAlarmDeviceId
      localStorage.setItem('networkForm',JSON.stringify(networkForm))
      this.$refs.networkDialog.dialogVisible = true; //
      this.networkPage.DeviceSn = item.deviceSn
      this.networkPage.fireAlarmDeviceId = item.deviceId
      this.networkPage.SkipCount = 0
      this.networkPage.current = 1
      
      this.GetDeviceDetectorList();//tablelist
      this.getneteorktypes_build(networkForm);//

    
    },
    //
    GetDeviceDetectorList(){
      this.loading = true
      this.$axios.get(this.$api.GetDeviceDetectorList,{params: this.networkPage}).then(res=>{
        console.log("",res);
        this.loading = false;
        ({items:  this.networkData,totalCount: this.networktotalCount} = res.data.result);
        if(res.data.result.items.length == 0 && res.data.result.totalCount>0){
            this.networkPage.SkipCount = 0
            this.GetDeviceDetectorList()
        }

      }).catch(err=>{
        console.log("",err)
      })
    },
    //table
    network_prev_next(page){
      this.networkPage.SkipCount = (page-1)*this.networkPage.MaxResultCount
      console.log("this.networkPage",this.networkPage)
      this.networkPage.current = page
      this.GetDeviceDetectorList();
    },
    //
    addNetworkcomponents(){
      this.networkForm ={}
      let networkForm = JSON.parse(localStorage.getItem('networkForm')) 
      this.networkForm.fireUnitArchitectureName =networkForm.fireUnitArchitectureName
      this.networkForm.fireUnitArchitectureId = networkForm.fireUnitArchitectureId
      this.networkForm.deviceSn = networkForm.deviceSn
      this.$refs.network_detailDialog.dialogVisible = true
     
     
    },
     //
    getneteorktypes_build(networkForm){
      console.log("")
      let fireunitId = localStorage.getItem('fireUnitID')
      this.$axios.all([       
        this.$axios.get(this.$api.GetFireAlarmDetectorTypes,{params:{}}),                                                                         
        this.$axios.get(this.$api.GetListByFireUnitId,{params:{fireunitId}})
      ]).then(this.$axios.spread((res0,res)=>{
        console.log("",res0)
        this.FireAlarmDetectorTypes = res0.data.result
        console.log(" ",res)
         console.log("",networkForm.fireUnitArchitectureName)
        if(res.data.result.length>0){
          for(let arr of res.data.result){
            if(networkForm.fireUnitArchitectureName == arr.name){
              this.fireUnitArchitecturefloors = arr.floors
            }
          }
        }
      }))
    },
    //
    add_updatenetwork(){
      console.log(this.networkForm)
      this.$refs.network_detail.validate(valid => {
        if(valid){
          let as = this.networkForm.detectorId ?
          this.$axios.put(this.$api.UpdateFireAlarmDetector,this.networkForm):
          this.$axios.post(this.$api.AddFireAlarmDetector,this.networkForm);
          as.then(res=>{
            console.log("",res)
            if(res.data.success){
              this.$refs.network_detailDialog.dialogVisible = false
              this.$message.success(
                      `${this.networkForm.detectorId ? "" : ""}`
                  );
              this.GetDeviceDetectorList(JSON.parse(localStorage.getItem('networkForm')).deviceSn );
            }
          }).catch(err=>{
            console.log("",err)
          })
        }
      })
    },
    //
    network_cancel(){

    },
    //
    showdetails_network(item){
      console.log("",item)
      this.$refs.network_detailDialog.dialogVisible = true
      this.networkForm = item
      // this.getneteorktypes_build();
    },
    //
    DeleteDetector(item){
      let DetectorId = item.detectorId
      this.$axios.delete(this.$api.DeleteDetector,{params:{DetectorId}}).then(res=>{
        console.log("",res);
      
          this.$message({
            message: '',
            type: 'success'
          });
           this.GetDeviceDetectorList(JSON.parse(localStorage.getItem('networkForm')).deviceSn );
      })
    },
    //------------------------
    Import(){
      this.$refs.network_importDialog.dialogVisible = true
      
    },
    //
    download(){
      let link = document.createElement('a')
      link.style.display = 'none'
      link.href = 'http://futest.sctsjkj.com/template/.xlsx';
      link.click();
    },  
    handleExceed(){
      this.$notify({
        title: '',
        message: '!,',
        type: 'warning'
      });
    },
    uploadFile(item){
      const form = new FormData();
      form.append('FireAlarmDeviceSn',this.networkPage.DeviceSn)
      form.append('file',item.file)
      this.$axios.post(this.$api.ImportFireAlarmDetector,form).then(res=>{
        console.log("res",res)
        if(res.data.result.success){
          this.$message({
            message: '!',
            type: 'success'
          });
          this.GetDeviceDetectorList();//tablelist
        }else{
          this.$message({
            showClose: true,
            message: `${res.data.result.failCause}`,
            type: 'error',
            duration:'6000'
          });
        }
        this.fileList =[]
        this.$refs.network_importDialog.dialogVisible = false
      }).catch(err=>{
        console.log("err",err)
      })

    },
    //
    beforeAvatarUpload(file){
      const extension = file.name.split(".")[1] === "xls";
      const extension2 = file.name.split(".")[1] === "xlsx";
      if (!extension && !extension2) {
           alert(" xlsxlsx!");
      }
    },
    submitUpload(){
      this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
      }).then(() => {
          this.$refs.upload.submit();
            
      }).catch(() => {

      });
    
    }
  }
}
</script>

<style lang="less">
  .firenetworkingBox{
    .green{
      color: rgb(12, 179, 12) !important;
    }
    .red{
     color: #cccccc;
    }

    //total
    .el-form-item__label{
      font-size: 14px;
      color: white;
    }
    .top_tips{
      width: 100%;
      display: flex;
      align-items: center; 
      justify-content: space-between;
      &>.el-button{
        width: 200px;
        background: #025691;
        color: white;
      }
    }
    // --------------------------------
    .formBox{
      width: 90%;
      padding: 20px;
      border: 1px solid darkblue;
      box-shadow: 0 2px 12px 0 #29d;
      .el-form-item{
        margin: 0px;
      }
      .el-form-item__content{
        width: 60%;
        .el-select{
          width: 100%;
        }
      }
      .smsPhones{
        margin-left: 70px;
      }
     /*  .el-select{
        width: 80%;
      } */
        .networkNum{
          .el-form-item__label{
            width: 120px !important;
            text-align: left;
          }
          .el-form-item__content{
            margin-left: 120px !important;
          }
        }
      .baseBox{
        display: flex;
        align-items: center;
        border: 1px dotted rgb(124, 123, 123);
        border-radius: 4px;
        padding: 14px 0px;
        .left_box{
          height: 80px;
          line-height: 80px;
          border-right:1px dotted rgb(124, 123, 123)
        }
        &:nth-of-type(2){
          margin: 20px 0px;
        }
      }
      .pd20{
        padding: 6px;
        display: flex;
        align-items: center;
      }
      .marginleft10{
        margin-left: 34px;
        color: white;
      }
      .basetitle{
        font-size: 16px;
        color: white;
      }
      .el-form-item__label{
        font-size: 14px;
        color: white;
      }
      .el-checkbox__label{
        color: #cccccc;
      }
      .el-input__inner{
        background: #025691;
        color: white;
        /* width: 80%; */
      }
      input::placeholder{
         color: #999898;
      }
      .el-select-dropdown__list{
        background: #011524;
      }
      .btns{
        margin-top: 40px;
        display: flex;
        justify-content: center;
        .el-button{
          width: 200px;
          background: #025691;
          color: #cccccc;
        }
      }
      
    }
    .el-dialog__body{
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    //--------------------------------
    .networkDialog{
        .topBox{
          display: flex;
          .el-button{
            width: 160px;
            color: #cccccc;
            background: #025691;
            &.add{
              color: white;
              border-color:rgb(11, 153, 219);
            }

          }
        }
        .baseTable_pagenation{
          width: 100%;
        }
    }
    // 
    .network_detail-form{
      width: 50%;
      .el-input__inner{
        background: #025691;
        color: #cccccc;
      }
      .el-select{
        width: 100%;
      }
      .build{
        padding-left: 40px;
        color: white;
        margin-bottom: 20px;
        span{
          margin-left: 16px;
        }
      }
      .el-button{
        width: 48%;
        background: #025691;
        color: white;
        &.cancel{
          background: #011524;
        }
      }
    }
    //
    .network_importDialog{
      .dialog_content{
        display: flex;
        flex-direction: column;
        border: 1px solid darkblue;
        width: 500px;
        border-radius: 8px;
        padding: 40px;
        box-sizing: border-box;
        box-shadow: 0 2px 12px 0 #29d;
        .tips{
          color: #cccccc;
          font-size: 16px;
        }
        p{
          line-height: 1.5;
          color: white;
        }
        .build{
          margin: 10px 0px;
          font-size: 16px;
          p{
            margin-bottom: 20px;
          }
          div{
            color: white;
          }
        }
        .bottom{
              width: 400px;
              margin-top: 10px;
              &>span{
                color: #0576c7;
                cursor: pointer;
                font-size: 16px;
              }
              .btn_box{
                margin-top: 16px;
                .el-button{
                  background:#011524;
                  color: #cccccc !important;
                }
              }
        }
        .el-upload-list{
          .el-upload-list__item:hover{
            a{
              color: #025691;
              .el-icon-document{
               color: #025691;
              }
           }
            
          }
          li{
            border: 1px solid darkblue;
            a{
              color: white;
              .el-icon-document{
                color: white;
              }
            }
          }
        }
 
      }
    }
    //
    .threedialog{
      .top_tips{
        color: white;
      }
      .baseTable_pagenation{
        width: 100%;
      }
    }
  }
 
</style>


